<template>
  <transition name="fade">
    <div v-if="show">
      <section>
        <div class="mx-md-5 text-center" style="width:100%">
          <div v-if="Currentquestion">
            <!-- compteur de question -->
              <div class="text-center"><span id="countera"> {{ timerCount }}</span></div>

            <p
              style="margin-bottom:0px;color: #5BE145;width:100%"
              class="text-uppercase font-weight-bold"
            >QUESTION {{Currentquestion.index+1}}</p>
          

            <!-- la question -->
            <h3 class="pb-4">{{ Currentquestion.question.question }}</h3>

            <!-- Les reponses -->
            <ul class="list-group" :class="[trouve === false ? 'disabled' : '']"
            v-for="(question, index) in shuffledAnswers"
              :key="index"
            >              
            <span class="" id="oe">
              <li class="list-group-item mt-3 text-center"
              :class="answersClass(index)"
              @click="selectAnswer(index)"
            >{{question}}</li>
            </span>
            </ul>

            <!-- <div
              class=" my-2" :class="[selectedIndex === index ? 'selected' : '']"
              v-for="(option, index) in answers"
              :key="index">
              <input type="radio" name="rGroup" :value="option" :id="option" />
              <label
                class="radio p-3"
                :for="option"
                style="width:100%"
                @click="selectAnswer(index)"
              >
                <span>{{option}}</span>
              </label>
            </div> -->

             <!-- Button -->
            <div class="d-flex justify-content-around">
                <button
                  type="button" v-if="trouve"
                  class="btn btn-block p-md-2 mt-4 btn-success" :class="[disabled === true ? 'button' : '']"
                  :disabled="disable_button"
                  @click="submitAnswer"
                >
                  <span >Valider</span>
                  <!-- <span v-else>Finish</span> -->
                </button>
                <!--next button-->
                <button v-else
                  type="button"
                  class="btn btn-info btn-block p-md-2 mt-4"
                  @click="goForward"
                >
                  <span>Continuer</span>
                  
                </button>
            </div>
          </div>
          <!-- <div v-else class="text-center">You are very Talented Student.</div> -->
        </div>
      </section>
    </div>
  </transition>

</template>

<script>
import _ from 'lodash';

export default {
  name: "QuestionBox",
  data() {
    return {
      timerCount: 15,
      selectedIndex: null,
      correctIndex: null,
      show: true,
      disabled:null,
      disable_button: true,
      // disable_next: true,
      shuffledAnswers: [],
      trouve: true,
      answered: false,
    };
  },
  props:{
    Currentquestion: Object,
    next: Function,
    increment: Function,
    Isincorrect: Function,
    isfalse: Number,
    },
    computed: {
      answers(){
        let answers = [...this.Currentquestion.question.incorrect];
         answers.push(this.Currentquestion.question.correct);
         return answers
      }
    },
    watch: {
      Currentquestion : {
        immediate: true,
        handler(){
          this.timerCount = 15
          this.selectedIndex = null
          this.answered = false
          this.shuffleAnswers()
        }
      },
      timerCount(){
        if(this.timerCount === 0){
          this.answersClass
          this.trouve = false
        }
      }
    },
  methods: {
    submitAnswer() {
        this.answered = true;
        if(this.selectedIndex === this.correctIndex){
          this.trouve = true
            this.next()
          
          // animation
          this.show = !this.show;
          
          setTimeout(() => (this.show = !this.show), 500);

          // disable submit button
          this.disable_button = true;
          // // disable next button
          // this.disable_next = false;

          let isCorrect = false;

          if (this.selectedIndex === this.correctIndex){
            isCorrect = true,
            this.increment(isCorrect);
        }
        } else{
          this.trouve = false
        }
    },
    goForward() {
      this.answered = true;
      this.Isincorrect()
      // next question
      if(this.isfalse > 1){
          this.next()
        // animation
        this.show = !this.show;
        setTimeout(() => (this.show = !this.show), 500);

        // disable submit button
        this.disable_button = true;
        this.trouve = true
      }

    },
    selectAnswer(index) {
      this.selectedIndex = index;
      // enable submit button
      this.disable_button = false;
    },
    shuffleAnswers() {
       let answers = [...this.Currentquestion.question.incorrect, this.Currentquestion.question.correct];
       this.shuffledAnswers = _.shuffle(answers)
       this.correctIndex = this.shuffledAnswers.indexOf(this.Currentquestion.question.correct)
    },
    answersClass(index){
      let answerClass = '';

      if(!this.answered && this.selectedIndex === index){
        answerClass = 'selected'
      } else if(this.answered && this.correctIndex === index){
        answerClass = 'correct'
      } else if(this.answered && this.selectedIndex === index && this.correctIndex !== index){
        answerClass = 'incorrect'
      }else if(!this.answered && this.correctIndex === index && this.timerCount === 0){
        answerClass = 'correct'
      }

      return answerClass;

    },
      autoIncrementTimer() {
            if (this.timerCount <= 0) {
                return
            }
            if(!this.answered){
              this.timerCount -= 1;
            }
        }

  },
  created() {
    window.setInterval(this.autoIncrementTimer, 1000)
  },
};
</script>

